import { flattenMessages } from './helpers/flatternMessages';
import en from './messages/en';
import fa from './messages/fa';
import ar from './messages/ar';

const messages = {
	fa: flattenMessages(fa),
	ar: flattenMessages(ar),
	en: flattenMessages(en),
};

const getMessages = (locale: string | number) => messages[locale];

export { messages, getMessages };
