import { useEffect } from 'react';
import useLocalStorage from './useLocaleStorage';

function usePrefersDarkMode() {
	const returnValue = typeof window !== `undefined` ? window.matchMedia('(prefers-color-scheme: dark)').matches : false;
	return returnValue;
}

function useTheme() {
	const initialTheme = usePrefersDarkMode() ? 'dark' : 'light';

	const [enabledTheme, setEnabledTheme] = useLocalStorage('theme', initialTheme);

	const theme = typeof enabledTheme !== 'undefined' ? enabledTheme : initialTheme;

	// Fire off effect that add/removes dark mode class
	useEffect(() => {
		const element = document.documentElement;
		if (theme == 'dark') {
			element.setAttribute('theme', 'dark');
		} else {
			element.setAttribute('theme', 'light');
		}
	}, [theme]); // Only re-call effect when value changes

	// Return enabled state and setter
	return [theme, setEnabledTheme];
}

export default useTheme;
