import * as React from 'react';
import * as styles from './ImageCompare.module.scss';

export const ImageCompare = ({ before, after }) => {
	if (typeof window !== 'undefined') {
		const { ImgComparisonSlider } = typeof window !== 'undefined' ? require('@img-comparison-slider/react') : null;
		return (
			<div className={styles.wrapper}>
				<ImgComparisonSlider>
					<img slot="before" src={before} />
					<img slot="after" src={after} />
				</ImgComparisonSlider>
			</div>
		);
	}
	return <div>Loading...</div>;
};
