import * as React from 'react';
import { IntlProvider } from 'react-intl';
import { locales } from '~src/_i18n/i18n';
import { messages as msg } from '~src/_i18n/messages';
import useLocale from '~src/context/Hooks/useLocale';
import { LocaleContextInterface } from '~src/types/intl';

const defaultLangContext: LocaleContextInterface = {
	activeLocale: null,
	setLocale: () => null,
	locales: null,
};

export const LocaleContext = React.createContext<LocaleContextInterface>(defaultLangContext);

export default function LocaleWrapper({ pageLocale, messages, children }: LocaleWrapperInterface) {
	const [locale, setLocale] = useLocale(pageLocale);
	const ssrMessages = messages ? messages : msg[locale];
	return (
		<LocaleContext.Provider
			value={{
				activeLocale: locale,
				setLocale,
				locales,
			}}>
			<IntlProvider locale={locale} messages={ssrMessages}>
				{children}
			</IntlProvider>
		</LocaleContext.Provider>
	);
}

interface LocaleWrapperInterface {
	pageLocale?: string;
	messages?: Record<string, string>;
	children: React.ReactElement;
}
