const transitionDelay = 20;

const shouldUpdateScroll = ({ routerProps: { location }, getSavedScrollPosition }) => {
	// To fix offcanvas scroll-to-top bug
	if (location.hash) {
		return false;
	}

	//For transitions
	if (location.action === 'PUSH') {
		window.setTimeout(() => window.scrollTo(0, 0), transitionDelay);
	} else {
		const savedPosition = getSavedScrollPosition(location);
		window.setTimeout(() => window.scrollTo(...(savedPosition || [0, 0])), transitionDelay);
	}
	return false;
};

export default shouldUpdateScroll;
